declare var $: any;




@Component({
  selector: 'app-editprofiletrainer',
  templateUrl: './editprofiletrainer.component.html',
  styleUrls: ['./editprofiletrainer.component.css'],
  providers: [Modal],
})
export class EditprofiletrainerComponent implements OnInit, HttpSuccesFailureResponse {


  profiledata: UserProfile;
  editTrainerForm: FormGroup;
  changepasswordForm: FormGroup;
  updatephoneForm: FormGroup;
  updateemailForm: FormGroup;
  gender = Constants.genderRegister;
  isChangePasswordClicked = false;
  isChangePhoneClicked = false;
  isChangeEmailClicked = false;
  isEmailAlreadyExist: boolean = false;
  isEmailAvailable: boolean = false;
  profileFileList: File[] = [];
  coverFileList: File[] = [];
  isShowSmallLoader: boolean = false;
  list: NationalityData[] = [];
  updateResponseResult: number = 0;
  errorMessage: string;
  dob: string;
  private subscription: Subscription;
  private timer: Observable<any>;
  userImage: string;
  typecover = false;
  trainerCoverImage: string;
  profilefilelength: boolean = false;
  coverfilelength = false;
  uploadImageProgess: boolean = false;
  trainerDetail: TrainerDetail;
  classBackgroundimagesList: MediaResponseDtoList[] = [];
  activitiesList: ClassCategoryList[] = [];
  deletedActivitiesList: number[] = [];
  activitiesListDataResponse: ActivitiesListData[] = [];
  isFieldValid: boolean = false;
  dataService: CompleterData;
  nationalityService: CompleterData;
  datetimestamp: any;
  location: string = "";
  redirectToHome: boolean = true;
  profileMainFile: any;
  coverMainFile: any;
  format: boolean = false;
  uploadError: string = "";
  uploadfailed: boolean = false;
  imageCropperSettings: CropperSettings = new CropperSettings();
  isBioIsValid: boolean = true;
  isCertificate: boolean = true;
  certificateErrorMessage = Constants.cerficateEditMessage;
  bioEditMessage = Constants.bioEditMessage;
  deletedActivitiesList1: number[] = [];
  validdob:boolean=false;
  @ViewChild("fileInput") fileInput: ElementRef;
  @ViewChild("coverFileInput") coverFileInput: ElementRef;






  constructor(private completerService: CompleterService, public modal: Modal, private http_user: HttpUserAuthUtilsService, private storageService: StorageService,
    private router: Router,
    private titleService: Title,
    private formBuilder: FormBuilder,
    private _sanitizer: DomSanitizer,
    private common_http: HttpCommonUtilsService,
    private globalSubscription: GlobalSubscriptionService) { }
  ngAfterViewInit() {
    $('#oldpassword').bind("cut copy", function (e) {
      e.preventDefault();
    });
    $('#newpassword').bind("cut copy", function (e) {
      e.preventDefault();
    });
    $('#confirmpassword').bind("cut copy", function (e) {
      e.preventDefault();
    });
  }
  ngOnInit() {

    this.getActivitiesList();
    this.common_http.getNationalityList(this);

    this.changepasswordForm = this.formBuilder.group({
      oldpassword: ['', Validators.required],
      password: ['', Validators.compose([Validators.required, , Validators.minLength(6), Validators.maxLength(20)])],
      confirmPassword: ['', Validators.compose([Validators.required, , Validators.minLength(6), Validators.maxLength(20)])]
    }, { validator: ValidationService.checkIfMatchingPasswords('password', 'confirmPassword') });

    this.updatephoneForm = this.formBuilder.group({
      phone: ['', [Validators.pattern('^[0-9]*$'), Validators.minLength(8), Validators.maxLength(16), Validators.required]],

    });
    this.updateemailForm = this.formBuilder.group({
      email: ['', ValidationService.emailValidator],

    });
    this.editTrainerForm = this.formBuilder.group({
      firstname: ['', [Validators.required, Validators.maxLength(250)]],
      lastname: ['', [Validators.required, Validators.maxLength(250)]],
      gender: [''],
      location: [''],
      nationality: [''],
      activity: [''],
      phone: [''],
      dob: [''],
      bio: ['', Validators.maxLength(600)],
      email: [''],
      password: [''],
      passwordchangedate: [''],
      profileImage: [''],
      certificates: ['', Validators.required]

    });
    this.http_user.getTrainerProfileDetail(LocalStorageService.getUserId(), this, true);
    this.fileInput.nativeElement.onclick = () => {
      this.fileInput.nativeElement.value = null;
    };
    this.coverFileInput.nativeElement.onclick = () => {
      this.coverFileInput.nativeElement.value = null;
    };

  }
  reditrectToHome() {
    this.router.navigate(['/'])
  }

  isLoggedIn() {
    return LocalStorageService.getIsLoggedIn();
  }

  switchUpdateUi(value) {
    this.errorMessage = "";
    switch (value) {
      case "Password": {
        this.isChangePasswordClicked = UserSession.switch(this.isChangePasswordClicked);
        if (this.isChangePasswordClicked) {
          this.redirectToHome = false;
          this.isChangePhoneClicked = false;
          this.isChangeEmailClicked = false;
          this.resetCahngePasswordForm();
        }
        break;
      }
      case "Phone": {
        this.isChangePhoneClicked = UserSession.switch(this.isChangePhoneClicked);
        if (this.isChangePhoneClicked) {
          this.redirectToHome = false;
          this.isChangePasswordClicked = false;
          this.isChangeEmailClicked = false;
        }
        break;
      }
      case "Email": {
        this.isChangeEmailClicked = UserSession.switch(this.isChangeEmailClicked);
        if (this.isChangeEmailClicked) {
          this.redirectToHome = false;
          this.isChangePasswordClicked = false;
          this.isChangePhoneClicked = false;
          this.isEmailAvailable = false;
          this.isEmailAlreadyExist = false;
        }
        break;
      }
    }

  }
  changePassword() {
    this.markFormGroupTouched(this.changepasswordForm)

    if (!this.changepasswordForm.valid && !this.changepasswordForm.dirty) {
      this.errorMessage = Constants.fieldRequiredMessage;
    }
    if (this.changepasswordForm.value.oldpassword === this.changepasswordForm.value.password) {
        this.errorMessage = 'Old password and new password is same';
        this.showAlert(33);
        return;
    }
    if (this.changepasswordForm.valid) {
      this.http_user.changeUserPassword(LocalStorageService.getUserId(), this.changepasswordForm.value.oldpassword, this.changepasswordForm.value.password, this.changepasswordForm.value.confirmPassword, this, true);

    }
  }

  changePhone() {
    if (!this.updatephoneForm.value.phone) {
      this.errorMessage = Constants.fieldRequired;
    }
    if (this.updatephoneForm.valid) {
      this.http_user.changePhonenumber(LocalStorageService.getUserId(), this.updatephoneForm.value.phone, this, true)
    }
  }
  changeEmail() {
    if (!this.updateemailForm.value.email) {
      this.errorMessage = Constants.fieldRequired;
    }
    if (this.updateemailForm.valid) {
      this.isShowSmallLoader = true;
      this.common_http.validate("email", this.updateemailForm.value.email, this, false);
    }

  }
  private showAlert(value) {
    this.updateResponseResult = value;
    var timerDuration = (value == 4 || value == 88) ? 2000 : 5000;
    this.timer = Observable.timer(timerDuration); // 5000 millisecond means 5 seconds

    this.subscription = this.timer.subscribe(() => {
      if (value == 4) {
        this.redirectToHome = true;
        if (this.redirectToHome) {
          this.reditrectToHome();
        }
      }
      this.updateResponseResult = 0;
    });


  }
  onBlur($event) {
    var value = this.updateemailForm.value.email;
    if (this.updateemailForm.valid) {
      this.isShowSmallLoader = true;
      this.common_http.validate($event.target.id, value, this, false);
    }
  }

  onFocus($event) {
    var value = this.updateemailForm.value.email;
    if (this.updateemailForm.controls.email.errors == null) {
      this.isEmailAlreadyExist = false;
      this.isEmailAvailable = false;

    }
  }
  // for edit class popup
  refreshonlynumber(data) {
    this.editTrainerForm.controls['phone'].setValue(data.data.mobile)
    this.isChangePhoneClicked = false;
  }

  refreshonlyEmail(data) {
    this.editTrainerForm.controls['email'].setValue(data.data.email)
    this.isChangeEmailClicked = false;
  }
  refreshonlypassword(data) {
    let passwordupdatedate = Constants.passwordChangedZeroDays;
    this.editTrainerForm.controls['passwordchangedate'].setValue(passwordupdatedate)

  }
  onSuccess(type: any, responsedata: any) {
    if (type == Constants.FindTrainerProfile) {
      this.trainerDetail = responsedata;
      if (responsedata.classCategoryList) {
        this.activitiesList = responsedata.classCategoryList;
      } else {
        this.activitiesList = [];
      }
      this.refreshdata();
      this.userImage = this.trainerDetail.profileImageThumbnail;
      this.trainerCoverImage = this.trainerDetail.coverImage;
      this.updateUserSavedData();

    }

    if (type == Constants.ValidateApi) {
      this.isShowSmallLoader = false;
      this.isEmailAlreadyExist = false;
      this.http_user.changeEmail(LocalStorageService.getUserId(), this.updateemailForm.value.email, this, true);
    }
    if (type == Constants.UpdateTrainerApi) {
      this.trainerDetail.firstName = responsedata.data.firstName;
      this.updateUserSavedData()
      this.reditrectToHome();
      this.openconfirmpopup(Constants.profileDetailUpdate, Constants.profileUpdate);

    }
    if (type == Constants.UpdateUserPhone) {
      this.refreshonlynumber(responsedata);
      this.showAlert(2);
    }
    if (type == Constants.UpdateUserPassword) {
      this.refreshonlypassword(responsedata);
      this.isChangePasswordClicked = false;
      this.showAlert(3);
      this.resetCahngePasswordForm();


    }
    if (type == Constants.UpdateUserEmail) {
      this.refreshonlyEmail(responsedata)
      this.isChangeEmailClicked = false;
      this.showAlert(1);

    }
    if (type == Constants.AllActivityListApi) {
      this.activitiesListDataResponse=UserSession.setStatusForList(responsedata.data);
      this.dataService = this.completerService.local(this.activitiesListDataResponse, 'name', 'name')

    }
    if (type === Constants.FileUploadApi) {
      if (!this.typecover) {
      this.uploadImageProgess = false;
      this.trainerDetail.profileImage = responsedata.data.mediaPath;
      this.trainerDetail.profileImageThumbnail = responsedata.data.thumbnailPath;
      this.trainerDetail.userProfileImageId = responsedata.data.id;
      this.userImage = responsedata.data.thumbnailPath;
      this.updateUserSavedData();
      this.openconfirmpopup(Constants.profilePictureUpdate, Constants.profileUpdate);
      } else {
      this.trainerCoverImage = responsedata.data.mediaPath;
      this.trainerDetail.coverImage = responsedata.data.mediaPath;
      this.trainerDetail.coverImageThumbnail = responsedata.data.thumbnailPath;
      this.trainerDetail.coverBackGroundImageId = responsedata.data.id;
      this.openconfirmpopup(Constants.coverPictureUpdate, Constants.profileUpdate);
      }


    }
    if (type === Constants.mediaUpdate) {
      if (!this.typecover) {
      this.uploadImageProgess = false;
      this.trainerDetail.profileImage = responsedata.data.mediaPath;
      this.trainerDetail.profileImageThumbnail = responsedata.data.thumbnailPath;
      this.userImage = responsedata.data.thumbnailPath;
      this.updateUserSavedData();
      this.openconfirmpopup(Constants.profilePictureUpdate, Constants.profileUpdate);
      } else {
        this.trainerCoverImage = responsedata.data.mediaPath;
        this.openconfirmpopup(Constants.coverPictureUpdate, Constants.profileUpdate);
      }

    }
    if (type == Constants.MultipleFileUploadApi) {
      this.showAlert(4);
    }
    if (type == Constants.FindCountryApi) {
      this.list = responsedata;
      this.list.forEach(element => {
        element.nationality = element.nationality + "," + " " + element.alpha_3_code
      });
      this.nationalityService = this.completerService.local(this.list, 'nationality', 'nationality')
    }
  }
  onFailure(type: any, response: string) {
    if (type === Constants.mediaUpdate) {
      this.uploadImageProgess = false;
      this.uploadError = response;
      this.uploadfailed = true;


    }
    if (type == Constants.MultipleFileUploadApi) {
      this.showAlert(44);
      this.errorMessage = response;
    }
    if (type == Constants.FileUploadApi) {
      this.uploadImageProgess = false;
      this.uploadError = response;
      this.uploadfailed = true;
    }
    if (type == Constants.ValidateApi) {
      this.isShowSmallLoader = false;
      this.isEmailAlreadyExist = true;
      this.isEmailAvailable = false;
    }
    if (type == Constants.FindTrainerProfile) {

    }
    if (type == Constants.UpdateTrainerApi) {
      this.errorMessage = response;
      this.showAlert(44);
    }
    if (type == Constants.UpdateUserPhone) {

      this.showAlert(22);
      this.errorMessage = response;
    }
    if (type == Constants.UpdateUserPassword) {
      this.showAlert(33);
      this.errorMessage = response;
      this.resetCahngePasswordForm();


    }
    if (type == Constants.UpdateUserEmail) {

      this.showAlert(11);
      this.errorMessage = response;


    }
  }

  profilePicUpdated(imageResult: ImageResult) {
    this.imageCropperSettings.keepAspect = true;
    this.imageCropperSettings.minHeight = 500;
    this.imageCropperSettings.minWidth = 500;
    this.imageCropperSettings.canvasHeight = 550;
    this.imageCropperSettings.canvasWidth = 1000;
    this.imageCropperSettings.width = 500;
    this.imageCropperSettings.height = 50;
    this.imageCropperSettings.croppedHeight = 500;
    this.imageCropperSettings.croppedWidth = 500;
    this.openImageCropper(imageResult, 'profile');
  }
  coverPicSelected(imageResult: ImageResult) {
    this.imageCropperSettings.keepAspect = true;
    this.imageCropperSettings.minHeight = 273;
    this.imageCropperSettings.minWidth = 1140;
    this.imageCropperSettings.canvasHeight = 400;
    this.imageCropperSettings.canvasWidth = 1140;
    this.imageCropperSettings.width = 1140;
    this.imageCropperSettings.height = 273;
    this.imageCropperSettings.croppedHeight = 273;
    this.imageCropperSettings.croppedWidth = 1140;
    this.openImageCropper(imageResult, 'cover');
  }

  openImageCropper(imageresult: ImageResult, type: any) {
    const parent = this;
    return this.modal.open(ImagePopupComponent, overlayConfigFactory({ imageresult, type, parent }, BSModalContext));

  }

  croppedImageResult(type, image) {
    this.uploadfailed = false;
    this.format = false;
    if (type === 'cover') {
    if (this.coverMainFile) {
      let index = this.coverFileList.indexOf(this.coverMainFile);
      this.coverFileList.splice(index, 1);
    }
    this.coverMainFile = image;
    this.coverFileList.push(this.coverMainFile);
    this.coverfilelength = false;
    if(!this.coverFileList.length) return;
    image.name = Date.now().toString().concat(image.extension);
    image.objectType = "user";
    image.mediaFor = "User_Cover";
    image.mediaType = "Image";
    image.objectDataId = LocalStorageService.getUserId();
    this.uploadPic('cover', image)
    } else {
    this.uploadImageProgess = true;
    if (this.profileMainFile) {
      let index = this.profileFileList.indexOf(this.profileMainFile);
      this.profileFileList.splice(index, 1);
    }
    this.profileMainFile = image;
    this.profileFileList.push(this.profileMainFile)
    this.profilefilelength = false;
    if (!this.profileFileList.length) return;
    this.userImage = "";
    image.name = Date.now().toString().concat(image.extension);
    image.objectType = "user";
    image.mediaFor = "UserProfile";
    image.mediaType = "Image";
    image.objectDataId = LocalStorageService.getUserId();
    this.uploadPic('profile', image)
  }
}
uploadPic(type: string, image) {
  const formData = new FormData();
  if (type === 'cover') {
    this.typecover = true;
    if (this.trainerDetail.coverBackGroundImageId) {
      this.http_user.updateMediaJS(this.trainerDetail.coverBackGroundImageId, image, this, false);
    } else {
        this.http_user.uploadMediaJS(image, this, false);
    }
  } else {
    this.typecover = false;
    if (this.trainerDetail.userProfileImageId) {
      this.http_user.updateMediaJS(this.trainerDetail.userProfileImageId, image, this, false);
    } else {
      this.http_user.uploadMediaJS(image, this, false);

    }
  }
}

  filesChange(fieldName: string, fileList: FileList) {

    this.uploadfailed = false;

    if (fileList[0].type === "image/gif") {
      this.format = true
      return;
    }
    this.format = false;
    this.uploadImageProgess = true;
    const formData = new FormData();
    if (!fileList.length) return;
    Array.from(Array(fileList.length).keys()).map(x => {
      formData.append("media", fileList[x]);
      formData.append("mediaFor", "UserProfile");
      formData.append("mediaType", "Image");
      formData.append("objectDataId", LocalStorageService.getUserId() + "");
      formData.append("objectType", "user");
    });
    this.userImage = "";
    if (this.trainerDetail.userProfileImageId) {
      this.http_user.updateMedia(this.trainerDetail.userProfileImageId, formData, this, false);
    } else {
      this.http_user.uploadMedia(formData, this, false);

    }

  }
  deleteActivities(value: ClassCategoryList) {
    let index = this.activitiesList.indexOf(value);
    this.activitiesList.splice(index, 1);
    let indexremove = this.deletedActivitiesList.indexOf(value.id);
    if (indexremove == -1) {
      this.deletedActivitiesList.push(value.id);
      this.deletedActivitiesList1.push(value.classCategoryId);
    }
  }
  addNewActivites($event) {
    this.isFieldValid = true;
    var trainerDetailMappingId = 0;
    let data: ClassCategoryList;

    this.activitiesList.forEach(element => {
      if (element.classCategoryId === $event.originalObject.id) {
        data = element;
      }
    });
    let indexremove = this.deletedActivitiesList1.indexOf($event.originalObject.id);
    if (indexremove > -1) {
      trainerDetailMappingId = this.deletedActivitiesList[indexremove];
    }
    if (!data) {
      data = { "id": trainerDetailMappingId, "classCategoryId": $event.originalObject.id, "classCategoryName": $event.originalObject.name };
    }
    if (indexremove > -1) {
      this.deletedActivitiesList.splice(indexremove, 1);
      this.deletedActivitiesList.splice(indexremove, 1);
    }
    if ($event.originalObject.id && this.activitiesList.indexOf(data) === -1) {
      this.activitiesList.push(data);

    }

  }
  onKey() {
    if (this.editTrainerForm.controls['activity'].value.length == 0) {
      this.isFieldValid = true;
    } else {
      this.isFieldValid = false;
    }
  }
  onTrainingCertificateChange(){
    this.isCertificate=true;
}
  getActivitiesList() {
    this.common_http.getAllActivity(this, false);

  }

  gettinguserdob(event) {
    this.dob = event
  }
  autocompleListFormatter = (data: any) => {
    let html = `<span class="form-control">${data.nationality}</span>`;
    return this._sanitizer.bypassSecurityTrustHtml(html);
  }
  editTrainerProfile() { 
    var categoryList: any[] = [];
    this.isBioIsValid = true;
    var isBioInvalid: boolean = true;
    this.isCertificate = false;
    this.validdob=false;
    if(this.editTrainerForm.value.dob == 'Month' || this.editTrainerForm.value.dob == 'Year' || this.editTrainerForm.value.dob == 'Date'){
      this.validdob=true;
      this.showAlert(88);
      return false;
    }
    if (this.activitiesList) {
      this.activitiesList.forEach(element => {
        categoryList.push({ "classCategoryId": element.classCategoryId, "id": element.id })
      });
    }
    let biostr = this.editTrainerForm.value.bio;
    if (biostr.replace(/\s/g, '').length > 600) {
      isBioInvalid = false;
      this.isBioIsValid = false;
    }

    if (this.trainerDetail.bio && (!biostr.replace(/\s/g, '').length)) {
      this.isBioIsValid = false;
    }
    let certificatestr = this.editTrainerForm.value.certificates;
    if (certificatestr && certificatestr.replace(/\s/g, '').length>0) {
      this.isCertificate = true;
    }
    if (categoryList.length > 0 && this.isBioIsValid && this.isCertificate && this.editTrainerForm.valid) {
      this.http_user.editTrainerProfile(LocalStorageService.getUserId(), this.editTrainerForm.value.firstname,
      this.editTrainerForm.value.lastname, this.editTrainerForm.value.gender, this.editTrainerForm.value.nationality,
       this.editTrainerForm.value.bio.replace(/(\r\n|\n|\r)/gm, ""), this.dob, "", categoryList, 
       this.editTrainerForm.value.certificates, this.location, this.deletedActivitiesList, this, true);
    } else {
      if (categoryList.length == 0) {
        this.showAlert(44);
        this.errorMessage = Constants.noActivitiesMsg;
      }
      if (!this.isBioIsValid) {
        if (!isBioInvalid) {
          this.errorMessage = Constants.bioExceedMessage;
          this.isBioIsValid = true;
          this.showAlert(45);
          
          
        }
        else {
          this.errorMessage = Constants.bioEditMessage;
          this.showAlert(44);
        }
      }


    }
  }


  refreshdata() {
    var passwordChanged, bio;
    if (!this.trainerDetail.lastPasswordResetDate) {
      passwordChanged = Constants.haventChangePassword;
    } else {
      passwordChanged = Constants.passwordChanged + UserSession.getdaysdiffrence(this.trainerDetail.lastPasswordResetDate) + " ago";
    }
    if (this.trainerDetail.bio) {
      bio = this.trainerDetail.bio

    } else {
      bio = "";

    }
    this.editTrainerForm = this.formBuilder.group({
      firstname: [this.trainerDetail.firstName, [Validators.required, Validators.maxLength(250)]],
      lastname: [this.trainerDetail.lastName, [Validators.required, Validators.maxLength(250)]],
      gender: [this.trainerDetail.gender],
      location: [this.trainerDetail.location],
      nationality: [this.trainerDetail.nationality],
      dob: [this.trainerDetail.dob],
      activity: [''],
      phone: [this.trainerDetail.mobile],
      bio: [bio.replace(/(\r\n|\n|\r)/gm, "")],
      email: [this.trainerDetail.email],
      password: [''],
      passwordchangedate: [passwordChanged],
      certificates: [this.trainerDetail.certificates, Validators.required]
    });


    if (this.trainerDetail.dob) {
      this.datetimestamp = this.trainerDetail.dob;
    }
    else {
      this.datetimestamp = "0000000000";
    }

    this.classBackgroundimagesList = this.trainerDetail.mediaResponseDtoList;

  }
  removeValidation() {
    this.isEmailAlreadyExist = false;
  }

  updateUserSavedData() {
    let userData: UserLogin = LocalStorageService.getUser();
    userData.data.firstName = this.trainerDetail.firstName;
    userData.data.lastName = this.trainerDetail.lastName;
    userData.data.gender = this.trainerDetail.gender;
    userData.data.profileImage = this.trainerDetail.profileImage;
    userData.data.profileImageThumbnail = this.trainerDetail.profileImageThumbnail;
    LocalStorageService.setUser(userData);
    this.globalSubscription.userProfileUpdate();
  }
  // nationalitySeleted(value) {
  //   this.editTrainerForm.value.nationality = value.nationality;
  // }
  nationalitySeleted(event) {
    if (event) {
      this.editTrainerForm.value.nationality = event.originalObject.nationality;
    }
    else if (event == undefined) {
      this.editTrainerForm.controls['nationality'].setValue('')
    }
  }
  openconfirmpopup(message, title) {
    return this.modal.open(ConfirmpopupComponent, overlayConfigFactory({ message, title }, BSModalContext));

  }
  resetCahngePasswordForm() {
    this.changepasswordForm = this.formBuilder.group({
      oldpassword: ['', Validators.required],
      password: ['', [Validators.required, Validators.minLength(6), Validators.maxLength(20)]],
      confirmPassword: ['', [Validators.required, Validators.minLength(6), , Validators.maxLength(20)]]
    }, { validator: ValidationService.checkIfMatchingPasswords('password', 'confirmPassword') });
  }
  private markFormGroupTouched(formGroup: FormGroup) {
    try {
      (<any>Object).values(formGroup.controls).forEach(control => {
        control.markAsTouched();
        if (control.controls) {
          control.controls.forEach(c => this.markFormGroupTouched(c));
        }
      });
    }
    catch (ex) { }
  }

}



